html {
scroll-behavior: smooth;
}
body {
font-family: 'Noto Sans HK', sans-serif;
background: #fff;
}
.slider {
margin-bottom: 30px;
position: relative;
}
.slider .owl-item.active.center .slider-card {
-webkit-transform: scale(1.15);
        transform: scale(1.15);
opacity: 1;
background: #000000; /* fallback for old browsers */

color: #fff;
}
.slider-card {
background: #fff;
padding: 0px 0px;
margin: 50px 15px 90px 15px;
border-radius: 5px;
box-shadow: 0 15px 45px -20px rgb(0 0 0 / 73%);
-webkit-transform: scale(0.9);
        transform: scale(0.9);
opacity: 0.5;
transition: all 0.3s;
}
.slider-card img {
border-radius: 5px 5px 0px 0px;
}
.owl-nav .owl-prev {
position: absolute;
top: calc(50% - 25px);
left: 0;
opacity: 1;
font-size: 30px !important;
z-index: 1;
}
.owl-nav .owl-next {
position: absolute;
top: calc(50% - 25px);
right: 0;
opacity: 1;
font-size: 30px !important;
z-index: 1;
}
.owl-dots {
text-align: center;
}
.owl-dots .owl-dot {
height: 10px;
width: 10px;
border-radius: 10px;
background: #ccc !important;
margin-left: 3px;
margin-right: 3px;
outline: none;
}
.owl-dots .owl-dot.active {
background: #f44336 !important;
}
:root {
  --primary: #fff;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background-color: #fff;
  background-color: var(--primary);
  color: #242424;
  border: 1px solid #fff;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid #fff;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #242424;
  transition: 250ms;
}

